import React, { Component } from "react";
import { connect } from "react-redux";
import {
  Auth,
  Basic,
  SchoolHierarchyStore,
  FC_AllSchoolHierarchyDetails,
  SchoolHierarchyInterface,
} from "../../actions";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Loading from "../../components/Loading/Loading";
import StudyHierarchy from "../../components/StudyHierarchy/StudyHierarchy";
import { StoreState } from "../../reducers";
import SCHOOL_HIERARCHY from "../../utils/school_hierarchy.json";

interface StudyHierarchyPageProps {
  auth: Auth;
  basic: Basic;
  schoolHierarchyStore: SchoolHierarchyStore;
  FC_AllSchoolHierarchyDetails: (
    callBack: (status: boolean, msg: string) => void
  ) => void;
}
interface StudyHierarchyPageState {
  study_hierarchy: SchoolHierarchyInterface[] | null;
  loading: boolean;
}

export class _StudyHierarchyPage extends Component<
  StudyHierarchyPageProps,
  StudyHierarchyPageState
> {
  constructor(props: StudyHierarchyPageProps) {
    super(props);

    this.state = {
      study_hierarchy: null,
      loading: false,
    };
  }
  componentDidMount = () => {
    this.setState({ loading: true });
    if (this.state.study_hierarchy === null) {
      // this.props.FC_AllSchoolHierarchyDetails(
      //   (status: boolean, msg: string) => {}
      // );
      this.setState({
        study_hierarchy: SCHOOL_HIERARCHY as SchoolHierarchyInterface[],
      });
    }
    this.setState({ loading: false });
  };
  render() {
    if (this.state.study_hierarchy === null || this.state.loading === true) {
      return (
        <div className="bg-white rounded-md p-4 flex flex-col items-center justify-center m-5">
          <Loading />
          <div className="text-xl">Loading, please wait...</div>
        </div>
      );
    }
    return (
      <DashboardContainer>
        {this.props.auth.school !== null ? (
          <StudyHierarchy
            school={this.props.auth.school}
            allHierarchy={this.state.study_hierarchy}
          />
        ) : (
          <div>Load all Schools</div>
        )}
      </DashboardContainer>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  schoolHierarchyStore,
}: StoreState): {
  auth: Auth;
  basic: Basic;
  schoolHierarchyStore: SchoolHierarchyStore;
} => {
  return {
    auth: auth,
    basic: basic,
    schoolHierarchyStore: schoolHierarchyStore,
  };
};

export const StudyHierarchyPage = connect(mapStateToProps, {
  FC_AllSchoolHierarchyDetails,
})(_StudyHierarchyPage);
