import React, { Component, Fragment } from "react";
import { IoIosArrowDown } from "react-icons/io";
import { MdAdd, MdClose } from "react-icons/md";
import { GrPowerReset } from "react-icons/gr";
import {
  AuthSchool,
  FC_GetSchoolHierarchy,
  FC_GetSchoolLevels,
  FilteredCombination,
  HierarchyDataType,
  SchoolHierarchyGet,
  SchoolHierarchyInterface,
} from "../../actions";
import { search } from "../../utils/functions";
import Loading from "../Loading/Loading";
import { RiTodoLine } from "react-icons/ri";
import AddNew from "./AddNew";
import { APP_URL } from "../../config/app.config";
import axios from "axios";
import { errorToText } from "../../utils/errors";

interface StudyHierarchyProps {
  school: AuthSchool;
  allHierarchy: SchoolHierarchyInterface[];
  fixedHeight?: boolean;
}
interface StudyHierarchyState {
  loading: boolean;
  school_hierarchy: SchoolHierarchyGet[] | null;
  school_hierarchy_data: SchoolHierarchyInterface[] | null;
  selected_level_code: string | null;
  selected_combination_code: string | null;
  filter_by_combination: boolean;
  search_comb: string;
  addNew: boolean;
  addingNew: boolean;
  success: string;
  error: string;
}

export const GetLoading = (): JSX.Element => {
  return (
    <div className="bg-white rounded-md p-4 flex flex-col items-center justify-center m-5">
      <Loading />
      <div className="text-xl">Loading, please wait...</div>
    </div>
  );
};

export class StudyHierarchy extends Component<
  StudyHierarchyProps,
  StudyHierarchyState
> {
  constructor(props: StudyHierarchyProps) {
    super(props);

    this.state = {
      loading: true,
      school_hierarchy: null,
      school_hierarchy_data: null,
      selected_level_code: null,
      selected_combination_code: null,
      filter_by_combination: false,
      search_comb: "",
      addNew: false,
      addingNew: false,
      success: "",
      error: "",
    };
  }
  setAddNew = (status: boolean) => {
    this.setState({
      addNew: status,
      addingNew: status !== true ? false : true,
    });
  };
  setAddingNew = (status: boolean) => {
    this.setState({ addingNew: status });
  };
  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };
  FilterSchoolHierarchy = (
    data: SchoolHierarchyGet[]
  ): SchoolHierarchyInterface[] => {
    let response: SchoolHierarchyInterface[] = [];
    for (let item of data) {
      let test = this.props.allHierarchy.find(
        (itm) =>
          itm.studyhierarchyid === item.shool_hierarch_id && item.status !== "0"
      );
      if (test !== undefined) {
        response.push(test);
      }
    }
    return response;
  };
  setLoadSchoolSubjects = () => {
    return FC_GetSchoolHierarchy(
      this.props.school.school_code,
      (status: boolean, response: SchoolHierarchyGet[] | null) => {
        this.setLoading(true);
        if (status === true && response !== null) {
          this.setState({
            school_hierarchy: response,
            school_hierarchy_data: this.FilterSchoolHierarchy(response),
            loading: false,
          });
        } else {
          this.setState({
            school_hierarchy: [],
            school_hierarchy_data: [],
            loading: false,
          });
        }
      }
    );
  };

  FilteredData = (): SchoolHierarchyInterface[] => {
    let res = this.state.school_hierarchy_data;
    if (res !== null) {
      if (
        this.state.selected_level_code !== null &&
        this.state.selected_level_code !== ""
      ) {
        res = res.filter(
          (itm) => itm.level_code === this.state.selected_level_code
        );
      }
      if (this.state.selected_combination_code !== null) {
        res = res.filter(
          (itm) => itm.combination_code === this.state.selected_combination_code
        );
      }
      return res;
    } else {
      return [];
    }
  };
  RemoveSchoolHierarchy = async (id: string) => {
    if (
      window.confirm(
        "Are you sure do you want to remove this subject on school?"
      ) === true
    ) {
      this.setLoading(true);
      try {
        const res = await axios.post(
          `${APP_URL}/schoolhierarch/delete/${id}`,
          {}
        );
        console.log("Success: ", res);
        if (res.status === 200) {
          this.setLoadSchoolSubjects();
          this.setState({
            error: "",
            success: "School subject removed successfully!",
          });
        }
      } catch (error) {
        this.setState({
          error: errorToText(error),
          success: "",
          loading: false,
        });
      }
    }
  };
  componentDidMount = () => {
    this.setLoadSchoolSubjects();
  };
  render() {
    if (
      this.state.school_hierarchy === null ||
      this.state.school_hierarchy === null ||
      this.state.loading === true
    ) {
      <GetLoading />;
    }
    return (
      <Fragment>
        <div
          className="mb-10"
          style={
            this.props.fixedHeight !== undefined
              ? { height: "60vh", overflow: "auto" }
              : {}
          }
        >
          <div>
            <div className="p-2 mb-5">
              <div className="flex flex-row items-center gap-3 justify-between">
                <div className="flex flex-col">
                  <div className="text-2xl font-bold">
                    SCHOOL SUBJECTS ASSIGNMENT
                  </div>
                  <div className="text-sm text-gray-500">
                    {this.props.school.school_name}(
                    {this.props.school.school_code})
                  </div>
                </div>
                <div
                  onClick={() => {
                    this.setAddingNew(true);
                    this.setAddNew(true);
                  }}
                  className={`border ${
                    this.state.addingNew === false
                      ? "bg-yellow-100 border-yellow-400"
                      : "bg-gray-100"
                  } text-black px-3 py-2 rounded text-center hover:text-accent-900 cursor-pointer font-bold`}
                >
                  {this.state.addingNew === true
                    ? "Loading"
                    : "Assign new subjects"}
                </div>
              </div>
              <div className="mt-4">
                <div>Filter by</div>
                <div className="flex flex-row items-center gap-3">
                  <select
                    onChange={(e) =>
                      this.setState({ selected_level_code: e.target.value })
                    }
                    value={
                      this.state.selected_level_code === null
                        ? ""
                        : this.state.selected_level_code
                    }
                    className="border border-gray-400 rounded px-3 py-2"
                  >
                    <option value="">Choose education level</option>
                    <option value="">View by all levels</option>
                    {FC_GetSchoolLevels(this.props.allHierarchy, {
                      type: HierarchyDataType.SCHOOL_LEVELS,
                      status: true,
                    }).levels.map((item, i) => (
                      <option key={i + 1} value={item.level_code}>
                        {item.level_name}
                      </option>
                    ))}
                  </select>
                  <div>
                    <div
                      onClick={() =>
                        this.setState({
                          filter_by_combination:
                            !this.state.filter_by_combination,
                        })
                      }
                      className="w-auto border border-gray-400 rounded px-4 py-2 bg-white flex flex-row items-center justify-between gap-10 cursor-pointer hover:border-accent-900 hover:text-accent-900"
                    >
                      {this.state.selected_combination_code === null ? (
                        <span>All- Click to filter by combination</span>
                      ) : (
                        <span>
                          {
                            FC_GetSchoolLevels(this.props.allHierarchy, {
                              type: HierarchyDataType.COMBINATIONS,
                              status: true,
                            }).combinations.find(
                              (itm) =>
                                itm.combination_code ===
                                this.state.selected_combination_code
                            )?.combination_name
                          }
                        </span>
                      )}
                      <div className="-mr-3">
                        {this.state.filter_by_combination === true ? (
                          <MdClose />
                        ) : (
                          <IoIosArrowDown />
                        )}
                      </div>
                    </div>
                    {this.state.filter_by_combination === true && (
                      <div className="absolute w-max bg-white px-3 py-3 rounded-b animate__animated animate__zoomIn animate__faster shadow-xl">
                        <div>
                          <div className="flex flex-row items-center w-full justify-between gap-2">
                            <input
                              value={this.state.search_comb}
                              onChange={(e) =>
                                this.setState({ search_comb: e.target.value })
                              }
                              placeholder="Search..."
                              type="text"
                              className="border border-gray-400 rounded px-3 py-2 text-sm w-full"
                            />
                            <div>
                              <div
                                onClick={() =>
                                  this.setState({
                                    filter_by_combination: false,
                                  })
                                }
                                className="bg-red-100 text-red-800 text-2xl p-1 rounded-full cursor-pointer hover:bg-red-600 hover:text-white"
                              >
                                <MdClose />
                              </div>
                            </div>
                          </div>
                          {this.state.selected_combination_code !== null && (
                            <div
                              onClick={() =>
                                this.setState({
                                  selected_combination_code: null,
                                  filter_by_combination: false,
                                })
                              }
                              className="flex flex-row items-center gap-3 text-gray-800 font-bold cursor-pointer hover:text-accent-900 px-3 py-1 my-2 rounded bg-gray-100 w-full"
                            >
                              <div>
                                <GrPowerReset className="text-xl" />
                              </div>
                              <span>Click to view all</span>
                            </div>
                          )}
                        </div>
                        <div
                          className="h-64 overflow-y-auto mt-1 animate__animated animate__fadeIn"
                          style={{ minWidth: "28rem" }}
                        >
                          {search(
                            FC_GetSchoolLevels(this.props.allHierarchy, {
                              type: HierarchyDataType.COMBINATIONS,
                              status: true,
                            }).combinations,
                            this.state.search_comb
                          ).length === 0 ? (
                            <div className="text-center text-lg font-bold p-4 px-10">
                              No result found!
                            </div>
                          ) : (
                            search(
                              FC_GetSchoolLevels(this.props.allHierarchy, {
                                type: HierarchyDataType.COMBINATIONS,
                                status: true,
                              }).combinations,
                              this.state.search_comb
                            ).map((item: FilteredCombination, i: number) => (
                              <div
                                key={i + 1}
                                className={`w-full flex flex-row items-center border-b ${
                                  this.state.selected_combination_code ===
                                  item.combination_code
                                    ? "border-yellow-100 bg-yellow-100 font-bold"
                                    : "border-gray-200"
                                } py-1 gap-4 hover:bg-yellow-100 cursor-pointer`}
                                onClick={() =>
                                  this.setState({
                                    selected_combination_code:
                                      item.combination_code,
                                    filter_by_combination: false,
                                  })
                                }
                              >
                                <div>
                                  <div className="rounded-full bg-yellow-50 border border-yellow-200 h-8 w-8 flex flex-row items-center justify-center font-bold">
                                    {i + 1}
                                  </div>
                                </div>
                                <div>{item.combination_name}</div>
                              </div>
                            ))
                          )}
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>
            {this.state.success !== "" && (
              <div
                onClick={() => this.setState({ success: "" })}
                className="bg-green-600 text-white font-bold pl-4 pr-2 py-2 text-lg rounded-md my-2 fixed right-10 cursor-pointer shadow-xl animate__animated animate__bounceInRight"
              >
                <div className="flex flex-row justify-between items-center gap-8">
                  <div>Success: {this.state.success}</div>
                  <div>
                    <MdClose className="text-2xl text-red-600 bg-red-100 rounded-full" />
                  </div>
                </div>
              </div>
            )}
            {this.state.error !== "" && (
              <div
                onClick={() => this.setState({ error: "" })}
                className="bg-red-600 text-white font-bold pl-4 pr-2 py-2 text-lg rounded-md my-2 fixed right-10 cursor-pointer shadow-xl animate__animated animate__bounceInRight"
              >
                <div className="flex flex-row justify-between items-center gap-8">
                  <div>Error: {this.state.error}</div>
                  <div>
                    <MdClose className="text-2xl text-red-600 bg-red-100 rounded-full" />
                  </div>
                </div>
              </div>
            )}
            <div className="bg-white rounded mx-2 border border-gray-200">
              <div className="w-full overflow-x-auto">
                <table className="min-w-full text-left">
                  <thead>
                    <tr className="font-semibold border-b text-base">
                      <th className="p-2 border-b-2 border-gray-300 text-center">
                        #
                      </th>
                      <th className="p-2 border-b-2 border-gray-300">
                        Subject
                      </th>
                      <th className="p-2 border-b-2 border-gray-300">Level</th>
                      <th className="p-2 border-b-2 border-gray-300">
                        Combination
                      </th>
                      <th className="p-2 border-b-2 border-gray-300">Grade</th>
                      <th className="p-2 border-b-2 border-gray-300">
                        Periods
                      </th>
                      <th className="p-2 border-b-2 border-gray-300"></th>
                    </tr>
                  </thead>
                  <tbody className="text-sm">
                    {this.state.school_hierarchy_data === null ? (
                      <tr>
                        <td colSpan={7}>
                          <GetLoading />
                        </td>
                      </tr>
                    ) : this.FilteredData().length === 0 ? (
                      <tr>
                        <td colSpan={7}>
                          <div className="text-center text-lg text-gray-500 p-3">
                            No result found
                          </div>
                        </td>
                      </tr>
                    ) : (
                      this.FilteredData().map((item, i) => (
                        <tr key={i + 1} className="hover:bg-yellow-50 group">
                          <td className="p-2 border-b text-center">{i + 1}</td>
                          <td className="p-2 border-b">
                            {item.course_code}-{item.course_name}
                          </td>
                          <td className="p-2 border-b">{item.level_name}</td>
                          <td className="p-2 border-b">
                            {item.combination_name}
                          </td>
                          <td className="p-2 border-b">{item.grade_name}</td>
                          <td className="p-2 border-b">
                            {item.teaching_period}
                          </td>
                          <td className="border-b">
                            <div className="flex justify-end">
                              <div
                                onClick={() => {
                                  console.log("Current item: ", item);
                                  if (this.state.school_hierarchy !== null) {
                                    let testId =
                                      this.state.school_hierarchy.find(
                                        (itm) =>
                                          itm.studyhierarchyid ===
                                          item.studyhierarchyid
                                      );
                                    console.log("Compared: ", testId);

                                    if (testId !== undefined) {
                                      this.RemoveSchoolHierarchy(
                                        testId.school_study_hierarchy_id
                                      );
                                    }
                                  }
                                }}
                                className="bg-red-50 text-red-800 px-3 py-2 mx-1 my-1 rounded font-bold group-hover:bg-red-700 group-hover:text-white cursor-pointer"
                              >
                                Remove
                              </div>
                            </div>
                          </td>
                        </tr>
                      ))
                    )}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>

        {/* Modal here */}
        {this.state.addNew === true && (
          <AddNew
            school={this.props.school}
            setAddNew={this.setAddNew}
            allHierarchy={this.props.allHierarchy}
            school_hierarchy_data={this.props.allHierarchy}
            setLoadSchoolSubjects={this.setLoadSchoolSubjects}
          />
        )}
      </Fragment>
    );
  }
}

export default StudyHierarchy;
